.tab{
  &-navigation {
    width: 100%;
    max-width: 1100px;
    margin: 0 auto 35px;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
  }
  &-box{
    display: none;
  }
  &-list{
    width: 100%;
    border: 2px solid $main-blue;
    li{
      width: 25%;
      &:last-child {
        .tab-item::before {
          display: none;
        }
      }
    }
  }
  &-item{
    position: relative;
    @include fsb;
    font-size: 24px;
    line-height: 32px;
    color: $main-blue;
    padding: 16px 22px;
    transition: all .3s ease-in;
    height: 100%;
    align-items: center;
    justify-content: center;
    margin-left: -1px;
    &::before {
      content: '';
      width: 1px;
      height: 80%;
      position: absolute;
      top: 10%;
      right: 0;
      background-color: $main-blue;
    }
    &.active,&:hover, &:focus {
      color: $default-color;
      background-color: $main-blue;
    }
  }
}